th {
  text-align: center;
}
td {
  padding: 5px 10px;
  cursor: default;
  user-select: none;
  min-width: 40px;
  text-align: center;
  &.calendar:hover {
    background-color: #ccc;
    cursor: pointer;
  }
  &.isset {
    background-color: #060;
    color: white;
    &:hover {
      background-color: #090;
    }
  }
  &.empty {
    background-color: #A00;
    color: white;
    &:hover {
      background-color: #F00;
    }
  }
}
.button {
  display: inline-flex;
  height: 34px;
  justify-content: center;
  align-items: center;
}
.modal-bg {
  position: fixed;
  background: rgba(0, 0, 0, .2);
  width: 100%;
  height: 100%;
  &.show {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &.hide {
    display: none;
  }
  z-index: 9;
}
.modal {
  z-index: 99;
  position: fixed;
  background-color: white;
  height: auto;
  width: 300px;
  border: 1px solid #ccc;
  .modal-header {
    display: flex;
    justify-content: space-between;
    .modal-header-title {
      font-size: 24px;
      padding: 0;
      margin: 0;
    }
    .modal-header-close {
      position: absolute;
      top: 10px;
      right: 10px;
      font-size: 24px;
      width: 40px;
      height: 40px;
      cursor: pointer;
      border: 1px solid #ccc;
      border-radius: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  .modal-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    button {
      width: 25%;
    }
  }
}

.form-group {
  width: 100%;
  margin-bottom: 10px;
  .form-input {
    width: 100%;
    border: 1px solid #ccc;
    padding: 5px;
    border-radius: 5px;
    &:focus {
      outline: none;
    }
  }
}

.un_payed {
  background-color: #aa0000;
  color: white;
  padding: 5px 10px;
}
.payed {
  background-color: #006600;
  color: white;
  padding: 5px 10px;
}
.weekend {
  color: #CCC;
}